import React from 'react';
import GithubCorner from 'react-github-corner';

const GithubLink = () => (
  <GithubCorner
    href="https://github.com/EmaSuriano/react-hotkey-tooltip"
    octoColor="#EE6C4D"
    bannerColor="#e0fbfc"
    size={80}
    direction="right"
  />
);

export default GithubLink;



